html, body, #app-main {
  height: 100%;
  width:100%;
  background: #f5f5f5;
  overflow-x: hidden;
}
ul, li {
  padding: 0px;
  margin: 0px !important;
  list-style-type: none;
  display: flex;
}
#app-main {
  width:100%;
  height:100%;
  position: relative;
}

.wrapper {
  display: flex;
  min-height: 100vh;
  flex-flow: column;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position:absolute;
}

.button {
  flex-shrink: 0;
}

.container {
  display: flex;
  align-items: stretch;
  flex: 1;
  width: 100%;
  max-height: 90%;
  min-height: 90%;
}
.md-button.md-primary, .md-button.md-primary {
  background-color: #63c1a0 !important;
}
.md-checked .md-checkbox-container {
  background-color: #63c1a0 !important;
  border-color: #63c1a0 !important;
}


