.adminPanel__wrapper {
    background: url('../../assets/img/another-background.png') no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
}

.adminPanel__content {
    margin-left: 17%;
}

.adminPanel__content p {
    font-size: 40px;
    font-family: "Roboto Slab";
    color: rgb(45, 51, 58);
    font-weight: bold;
}

.adminPanel__block {
    width: 83%;
    background: white;
    padding: 20px 40px;
    border-radius: 3px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.adminPanel__img {
    margin-left: 15%;
    margin-right: 3%;
    margin-top: 9px;
}

.adminPanel__userSelect {
    width: 100%;
    display: flex;
    justify-content: center;
}

.adminPanel__userSelect-input {
    margin: 0;
    width: 40%;
    margin-right: 10%;
    text-align: left;
}

.adminPanelSettings__wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
}

.system__settings,
.news__settings,
.chat__settings {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.adminPanelSettings__wrapper p {
    font-size: 22px;
    font-family: "Roboto";
    color: rgb(27, 27, 27);
}

.adminPanelSettings__wrapper label {
    font-size: 18px;
    font-family: "Roboto";
    color: rgb(27, 27, 27);
}

.adminPanel__img img {
    width: 44px;
}


