.header {
  border-bottom: 1px solid #e2dede;
  height: 10%;
  min-height: 68px;

  &__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    background-color: #ffffff !important;
    color: #2d333a !important;
  }
  
  &__left {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__buttons {
    margin-right: 70px;
  }
}
.header img {
  margin-left: 13%;
}
.active {
  background-color: #63c1a0 !important;
}
.header .md-toolbar {
  min-height: 100% !important;
}


