.news__wrapper {
    background: url('../../assets/img/news-background.png') no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
}

.news__content {
    margin-left: 17%;
}

.news__content p {
    font-size: 40px;
    font-family: "Roboto Slab";
    color: rgb(45, 51, 58);
    font-weight: bold;
}

.new__wrapper {
    width: 83%;
    background: white;
    padding: 20px 40px;
    border-radius: 3px;
    text-align: center;
    margin-bottom: 2%;
}

.new__wrapper-edit {
    width: 83%;
    background: white;
    padding: 20px 40px;
    border-radius: 3px;
    text-align: center;
    margin-bottom: 2%;
}

.new__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2%;
}

.new__content-author {
    width: 100%;
    text-align: left;
    margin-left: 5%;
}

.new__content {
    display: flex;
    flex-direction: column;
    text-align: justify;
}

.new__content-userName {
    font-size: 18px;
    font-family: "Roboto";
    color: rgb(27, 27, 27);
}

.new__content-header {
    font-size: 22px;
    font-family: "Roboto";
    color: rgb(27, 27, 27);
    margin-bottom: 2%;
}

.new__wrapper-editContent {
    display: flex;
    flex-direction: row;
}

.new__wrapper-left {
    width: 35%;
    margin-right: 10px;
}

.new__wrapper-right {
    width: 75%;
    margin-right: 10px;
}

.addNews {
    position: absolute !important;
    right: 14%;
    top: 100px;
}

.new__content-img img {
    max-width: 44px;
}


