.settings__wrapper {
	background: url('../../assets/img/another-background.png') no-repeat;
	background-position: center center;
	background-size: cover;
	width: 100%;
}
.settings__content {
  margin-left: 17%;
}
.settings__content p {
  font-size: 40px;
  font-family: "Roboto Slab";
  color: rgb(45, 51, 58);
  font-weight: bold;
}
.settings__block {
  width: 83%;
  background: white;
  padding: 20px 40px;
  border-radius: 3px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.settiings__img-wrapper {
	width: 30%;
	margin-top: 20px;
}
.settiings__img-wrapper p {
  font-size: 18px;
  font-family: "Roboto";
  color: rgba(60, 60, 60, 0.702);
  text-decoration: underline;
}
.settings__userInfo-wrapper {
	width: 60%;
}
.settings__userInfo-wrapper p {
  font-size: 22px;
  font-family: "Roboto";
  color: rgb(27, 27, 27);
  text-align: left;
}
.settings__inputs-wrapper {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
.settings__inputs-wrapper div {
	width: 48%;
}
.settings__userButtons-wrapper {
	display: flex;
	justify-content: space-between;
}
.settings__userButtons-wrapper button {
	width: 45%;
}


