.chat__wrapper {
    background: url('../../assets/img/another-background.png') no-repeat;
    background-position: center center;
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: center;
}

.chat__content {
    width: 83%;
    background: white;
    border-radius: 3px;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.chat__left-wrapper {
    width: 30%;
    background: #494949;
}

.chat__right-wrapper {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.messages-wrapper {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    padding-top: 10px;
}

.userMessage-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
}

.userMessage-wrapper div {
    margin: 2%;
}

.chat__left-wrapper>div {
    color: white;
}

.chat__left-wrapper p {
    margin-left: 10%;
    text-align: left;
    text-decoration: underline;
}

.chat__left-wrapper ul {
    display: flex;
    flex-direction: column;
}

.chat__left-wrapper li {
    padding-left: 10%;
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
}

.chat__left-wrapper li:hover {
    background: #63c1a0;
}

.chat__left-wrapper i {
    margin: 0px !important;
    color: white !important;
    margin-right: 10px !important;
}

.chat__groupRooms-wrapper {
    margin-top: 10%;
}

.my {
    margin-right: 15px;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;
}

.sender {
    margin-left: 15px;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 10px;
}

.my .message_text {
    background: #63c1a0 !important;
    padding: 10px;
    border-radius: 5px;
    color: white;
}

.sender .message_text {
    background: #f4511e !important;
    padding: 10px;
    border-radius: 5px;
    color: white;
}

.active_chat {
    background: #f4511e;
}


