.main__wrapper {
  background: url('../../assets/img/background.png') no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
}
.main__content {
  margin-left: 13%;
}
.main__content p {
  font-size: 40px;
  font-family: "Roboto Slab";
  color: rgb(45, 51, 58);
  font-weight: bold;
}
.authForm__wrapper {
  width: 30%;
  background: white;
  padding: 20px 40px;
  margin-left: 1%;
  border-radius: 3px;
  text-align: center;
}
.authForm__submit {
  width: 100%;
  margin: 0;
  background: #63c1a0;
  color: white;
}
.authForm__submit:hover {
  background: #3f51b5 !important;
}
.reg__p {
  font-size: 16px !important;
  font-family: "Roboto" !important;
  color: rgba(43, 43, 43, 0.8) !important;
  margin-left: 6%;
}
.reg__link {
  color: #e45c80 !important;
  text-decoration: underline;
  cursor: pointer;
}
input:-webkit-autofill {
	-webkit-box-shadow: inset  0 1px 4px 0 white, /*   */
	                    inset 0 0 0 50px white, /*   */
	                    0 1px white !important; /*   */
	-webkit-text-fill-color: rgb(45, 51, 58) !important;
}


